.Player {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}
.Player.reversed {
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
}

.Player.reversed .lifeDisplay {
    -webkit-transform: scale(-1, -1);
        -ms-transform: scale(-1, -1);
            transform: scale(-1, -1);
}

.lifeDisplay {
    font-size: 20vh;
    font-weight: 600;
}

.lifeChangeEventBubble {
    display: none;
    position: absolute;
    font-size: 6vh;
    font-weight: 600;
    color: white;
    background-color: silver;
    border-radius: 4vh;
    padding: 2vh;
}

.lifeChangeEventBubble.show {
    display: block;
}

.Player:not(.reversed) .lifeChangeEventBubble {
    top: 0px;
    right: 6vh;
}

.Player.reversed .lifeChangeEventBubble {
    -webkit-transform: scale(-1, -1);
        -ms-transform: scale(-1, -1);
            transform: scale(-1, -1);
    bottom: 0px;
    left: 6vh;
}



